@import url('https://fonts.googleapis.com/css2?family=Lato:ital@0;1&display=swap');
* {
    margin: 0;
    padding: 0;
    font-family: 'Lato', sans-serif;
}

.itemLink {
    /*width: 100%;*/
    margin: 10px;
    padding: 10px;
    background: orange;
    border-radius: 5px;
    display: flex;
    text-decoration: none;
    color: white;
}
.itemLink img {
    width: 30px;
}

.Files{
    box-sizing: border-box;
    text-align: center;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100vw;
    height: 80vh;
    display: flex;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
}